import react from 'react';
import { useParams } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { addCount, subtractCount, deleteItem } from './../store';
import Postcode from './Postcode';

function Cart(){
  
  //redux
  let state = useSelector((state)=>{return state})
  let dispatch = useDispatch();
  let sumAll = state.cart.reduce((prev, curr) => {return prev + curr.totalPrice}, 0) + 3000;
  
  return (
    <div className="Cart">
      <h2 className="componentHead"></h2>
      <h4> </h4>
      <table className="selected">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {state.cart.map((a, i) => {
            return (
              <tr key={i}>
                <td className='justLeft cartImage'><img src={process.env.PUBLIC_URL + state.cart[i].image}/></td> {/*  */}
                <td>{state.cart[i].title}</td> {/*  */}
                <td>{state.cart[i].count}</td> {/*  */}
                <td>
                  <button className="countUp" onClick={()=>{ {/*  */}
                    dispatch(addCount(state.cart[i].id))
                  }}>  </button>
                  <button className="countDown" onClick={()=>{ {/*  */}
                    if(state.cart[i].count > 1){
                      dispatch(subtractCount(state.cart[i].id))    
                    }
                    else if(state.cart[i].count = 1){
                      return 1;
                    }           
                  }}>  </button>
                </td>
                <td>{state.cart[i].totalPrice.toLocaleString('ko-KR')}</td> {/*  */}
                <td> <button className='deleteBtn' onClick={()=>{ {/*  */}
                  dispatch(deleteItem())
                }}>  </button></td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <i> 3,000 .</i>
      <Postcode sumAll={sumAll}/>
    </div>
  );
};


export default Cart;