import react, {useState} from 'react';
import DaumPostcode from 'react-daum-postcode';
import Modal from 'react-modal';

function Postcode({sumAll}){

  let [zipCode, setZipCode] = useState('');
  let [roadAddress, setRoadAddress] = useState('');
  let [detailAddress, setDetailAddress] = useState('')
  let [isOpen, setIsOpen] = useState(false)

  //complete  state 
  const completeHandler = (data)=>{
    setZipCode(data.zonecode);
    setRoadAddress(data.roadAddress);
    setIsOpen(false);
  }
  // 
  const toggle = ()=>{
    setIsOpen(!isOpen);
  }
  // 
  const changeHandler = (e)=>{setDetailAddress(e.target.value)}
  //
  let optimizedStyle ={
    overlay: {backgroundColor: "rgba(0,0,0,0.5)"},
    content: {
      margin: "auto",
      padding: "0",
      overflow: "hidden",
    },
  }
  
  return (
    <div>
      <h4> </h4>
      <table className="reciever">
        <tbody>
          <tr>
            <th></th>
            <td>
              <input className="recieverName" type="text" maxLength={8} />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input
                className="phoneNum"
                type="text"
                placeholder=" "
                maxLength={12}
              />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <div>
                <input value={zipCode} readOnly placeholder='' className="postNum"/>
                <span onClick={toggle} className="searchPost">/ </span>
              </div>
              <div>
                <input value={roadAddress} readOnly placeholder=' ' className="post" type="text" />
              </div>
              <Modal style={optimizedStyle} isOpen={isOpen} ariaHideApp={false}>
                <DaumPostcode onComplete={completeHandler} />
              </Modal>
              <div>
                <input onChange={changeHandler} value={detailAddress} placeholder='' className="post" type="text" />
              </div>
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input className="request" type="text" maxLength={40} placeholder=".     ."/>
              <p className="underRequest"> 40  </p>
            </td>
          </tr>
        </tbody>
      </table>
      <h4> </h4>
      <table className="payment">
        <tbody>
          <tr>
            <th> </th>
            <td className="sumAll">{sumAll.toLocaleString("ko-KR")} <span className="underRequest"> ( 3,000 )</span></td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input className="credit" type="radio" name="payment" checked="checked"/> 
              <span className="space"></span>
              <input className="credit kakaopay" type="radio" name="payment" /> 
            </td>
          </tr>
        </tbody>
      </table>
      <div className="checkPay">
        <input type="checkbox" />
        <p>   ,      .</p>
      </div>
      <div className="orderArea">
        <button className="orderBtn2"></button>
      </div>
    </div>
  );
};


export default Postcode;