import './App.css';
import {useState, useEffect} from 'react'; 
import {Routes, Route, useNavigate} from 'react-router-dom';

import data from './data';
import chiefdata from './chiefdata';

import Nav from './component/Nav';
import Slider from './component/Slider';

import MainShowCase from './component/MainShowCase';
import Detail from './component/Detail';
import Cart from './component/Cart';

import Parallels from './component/Parallels';
import Announce from './component/Announce';
import Footer from './component/Footer';
import Quick from './component/Quick';


function App() {

  
  //
  const [pang, setPang] = useState(data);
  const [chief,setChief] = useState(chiefdata);
  let navigate = useNavigate()

  // 
  const scrollToTop = ()=>{
    window.scrollTo({
    top: 0,
    behavior: 'smooth'
  })}

  //sessionStorage window.onload   
  let storage = JSON.parse(sessionStorage.getItem('watched'))

  useEffect(()=>{
    let getStorage =sessionStorage.getItem('watched')
    JSON.parse(getStorage);
    //sessionStorage
    if(storage == null){
      sessionStorage.setItem('watched', JSON.stringify([ ]))
    }
  });
  
   
  
  return (
    <>
    <Nav/>

    <Routes>
      <Route path="/" element={<div><Slider/><MainShowCase storage={storage} pang={pang} chiefdata={chiefdata} scrollToTop={scrollToTop} /><Announce/></div>}/>
      <Route path="/detail/:id" element={<div><Detail pang={pang} setPang={setPang} chief={chief}/><Announce/></div>}/>
      <Route path="/cart" element={<div><Cart /></div>}/>
      <Route path="*" element={<div className="page404"><h3>    .</h3></div>}/>
    </Routes>

    
    <Footer/>
    <Quick scrollToTop={scrollToTop}/>
    </>
  );
}
export default App;
